import {Component, Inject} from "@angular/core";
import {MAT_DIALOG_DATA, MatDialogRef, MatDialog} from "@angular/material/dialog";
import {Router, ActivatedRoute} from "@angular/router";
import {AVTranscriptComponent} from "../av-transcript/av-transcript.component";

@Component({
  selector: 'complete-av',
  templateUrl: './complete-av.component.html'
})

export class CompleteAVActivityDialogComponent {
  name: string;
  maxLength: number;

  router: Router;
  route: ActivatedRoute;
  completed: boolean;
  conversation: [{ statementKey: string; speakerNumber: number; speakerName: string; image: string; text: string; extra: string }];

  constructor(public dialogRef: MatDialogRef<CompleteAVActivityDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any, router: Router, route: ActivatedRoute, public dialog: MatDialog) {
    this.router = router;
    this.route = route;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  viewTranscript(): void {
    this.onNoClick();
    const dialogRef = this.dialog.open(AVTranscriptComponent, {});
    const instance = dialogRef.componentInstance;
    instance.conversation = this.conversation;
  }

  goBack(): void {
    this.onNoClick();
    this.router.navigate(['/activity']);
  }

}



