import {Component, Inject} from "@angular/core";
import {MatDialogRef, MAT_DIALOG_DATA} from "@angular/material/dialog";
import {ActivatedRoute, Router} from "@angular/router";

@Component({
  selector: 'av-transcript',
  templateUrl: './av-transcript.component.html',
  styleUrls: ['./av-transcript.component.css']
})

export class AVTranscriptComponent {
  name: string;
  private router: Router;
  private route: ActivatedRoute;
  conversation: [{ statementKey: string; speakerNumber: number; speakerName: string; image: string; text: string; extra: string }];

  constructor(public dialogRef: MatDialogRef<AVTranscriptComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any, router: Router, route: ActivatedRoute) {
    this.router = router;
    this.route = route;
  }

  onNoClick(): void {

    this.router.navigate(['/activity']);
    this.dialogRef.close();
  }

  constructImageName(imageName: string) {
    return "../../assets/av/"+ imageName + "_small.png";
  }
}



