import {Component, ElementRef, OnInit, ViewChild} from "@angular/core";
import {Router, ActivatedRoute} from '@angular/router';

import {CaseStudyDialogComponent} from "../simulated-meeting/casestudy-dialog/casestudy-dialog.component";
import {CompleteActivityDialogComponent} from "../complete-activity/complete-activity.component";
import {ExitDialogComponent} from "../exit/exit-component";
import {CvDialogComponent} from "../simulated-meeting/cv-dialog/cv-dialog.component";
import {Candidate} from "./candidate";
import {MatDialog} from "@angular/material/dialog";
import {AvGameService} from "./av-game.service";
import {AVDialog} from "./av-dialog";
import {CompleteAVActivityDialogComponent} from "../complete-av/complete-av.component";
import {PanelistDialogComponent} from "../simulated-meeting/panelist-dialog/panelist-dialog.component";

@Component({
  selector: 'annotated-vita',
  templateUrl: './av-game.component.html',
  styleUrls: ['./av-dialog.component.css']
})

export class AVDialogComponent implements OnInit {


  progressValue: number;
  progressStep: number;

  candidates: Candidate[];
  selectedCandidate: Candidate;
  router: Router;


  id: number;
  route: ActivatedRoute;

  candidate: Candidate;
  private clicked: boolean;
  private candidateService: AvGameService;
  public dialogs: AVDialog;
  counter: number;

  conversationDone: [{ statementKey: string; speakerNumber: number; speakerName: string; image: string; text: string; extra: string }];
  image: string;
  currentDialog: string;

  constructor(router: Router, route: ActivatedRoute,
              public dialog: MatDialog, candidateService: AvGameService) {
    this.router = router;
    this.route = route;
    this.candidateService = candidateService;
  }

  ngOnInit(): void {
    this.clicked = false;
    this.counter = 0;
    this.getCandidate();
    this.getDialogs();

    this.conversationDone = [{
      statementKey: "",
      speakerNumber: 0,
      speakerName: "",
      image: "",
      text: "",
      extra: ""

    }]

  }

  getCandidate(): void {
    this.candidateService.getCandidates().then(candidates => {
      this.candidate = candidates[0];
    });
  }

  getDialogs(): void {
    this.candidateService.getDialogs().then(dialogs => {
      this.dialogs = dialogs[0];
      this.conversationDone.pop();
      this.conversationDone.push(this.dialogs.conversation[0]);
      this.image = this.dialogs.conversation[0].image;
      this.currentDialog = this.dialogs.conversation [0].text;
      this.progressStep = 100 / this.dialogs.conversation.length;
      this.progressValue = this.progressStep;
    })
  }



  clickOnNextButton(): void {

    if (this.counter === this.dialogs.conversation.length - 1) {
      this.openCompleteAVActivityDialog();
    }
    else {
      this.createDialogList();
    }

  }


  private createDialogList() {
    this.counter++;
    this.conversationDone.push(this.dialogs.conversation[this.counter]);
    this.image = this.dialogs.conversation[this.counter].image;
    this.currentDialog = this.dialogs.conversation[this.counter].text;
    this.progressValue += this.progressStep;
  }

  clickOnBackButton() {
    this.counter--;
    this.conversationDone.pop();
    this.image = this.dialogs.conversation[this.counter].image;
    this.currentDialog = this.dialogs.conversation[this.counter].text;
    this.progressValue -= this.progressStep;
  }


  openCvDialog() {
    const dialogRef = this.dialog.open(CvDialogComponent, {});
    const instance = dialogRef.componentInstance;
    instance.selectedCandidate = this.candidate;
  }

  openExitDialog() {
    const dialogRef = this.dialog.open(ExitDialogComponent, {});
    const instance = dialogRef.componentInstance;
    instance.activity = 3;
  }

  openCompleteAVActivityDialog(){

    const dialogRef = this.dialog.open(CompleteAVActivityDialogComponent, {disableClose: true});
    const instance = dialogRef.componentInstance;
    instance.conversation = this.conversationDone;

  }


  openCasestudyDialog() {
    const dialogRef = this.dialog.open(CaseStudyDialogComponent, {});
    const instance = dialogRef.componentInstance;
    instance.selectedCandidate = this.candidate;

  }


  goBack() {
    this.router.navigate(['/activity']);
  }

  openPanelistBio() {
    const dialogRef = this.dialog.open(PanelistDialogComponent, {});
    const instance = dialogRef.componentInstance;
    instance.committee_members = this.dialogs.committee_members;
    instance.title = "Characters";
  }

}



